<template>
    <div class="wrapper">
        <image :src="logo" class="logo"></image>
        <div class="login-form">
            <div class="form-box">
                <input class="form-control" type="text" placeholder="" v-model="user" />
            </div>
            <div class="form-box">
                <input class="form-control" type="password" placeholder="" v-model="password" />
            </div>
            <div class="btn-submit" @click="login">
                <text></text>
            </div>
        </div>
    </div>
</template>

<script>
    const modal = weex.requireModule('modal');
    const navigator = weex.requireModule('navigator');
    const stream = weex.requireModule('stream');
    export default {
        data () {
            return {
                logo: 'http://erdpsit.e-lead.cn//static/images/logo.jpg',
                user: '',
                password: ''
            };
        },
        methods: {
            login () {
                var param = 'rememberMe=true';

                if (!this.user) {
                    return modal.toast({
                        message: '',
                        duration: 0.5
                    });
                }
                param += '&userName=' + this.user;

                if (!this.password) {
                    return modal.toast({
                        message: '',
                        duration: 0.5
                    });
                }
                param += '&password=' + this.password;

                stream.fetch({
                    method: 'POST',
                    type: 'json',
                    headers: {
                        'Content-Type':'application/x-www-form-urlencoded'
                    },
                    body: param,
                    url: 'http://erdpsit.e-lead.cn/passport/v1/login'
                }, (res) => {
                    if (res.ok) {
                        navigator.push({
                            url: 'http://192.168.2.35:8080/pages/profile/index.js',
                            animated: 'true'
                        });
                    }
                });


                
            }
        }
    };
</script>

<style scoped>
.wrapper {
    justify-content: center;
    align-items: center;
}
.logo {
    width: 145px;
    height: 49px;
}
.login-form {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-content: space-between;
    align-items: center;
}
.form-box {
    border-bottom-width: 2px;
    border-style: solid;
    border-color: #41b883;
    height: 84px;
    line-height: 84px;
}
input {
    justify-content: space-between;
    width: 710px;
    height: 84px;
    line-height: 84px;
    padding: 0 20px;
    line-height: 84px;
}
.btn-submit {
    width: 500px;
    padding: 20px 0;
    margin: 98px 10% 0;
    align-items: center;

    border-width: 2px;
    border-style: solid;
    border-color: #41b883;
}
</style>



